#navbarContainer {
    background-color: #121212;
    padding: 20px;
}

#navbarContainer span {
    display: inline-block;
    margin: 5px 10px;
    text-transform: uppercase;
    opacity: 0.6;
    cursor: pointer;
}

#navbarContainer span:hover {
    opacity: 1;
}

#logoutBtn {
    float: right;
}
.bookmark {
    background-color: #121212;
    border: 2px solid #333333;
    border-radius: 5px;
    display: inline-block;
    width: 250px;
    height: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 8px;
    padding: 20px;
    transition: 0.2s;
}

.bookmark:hover {
    border: 2px solid #fdccff;
    box-shadow: 0px 0px 15px #7b0080;
}

.bookmark h3 {
    text-transform: capitalize;
    margin-bottom: 5px;
}

.bookmark a {
    text-decoration: none;
    color: white;
    opacity: 0.5;
    font-size: 12px;
    margin-top: 10px;
    margin-left: 5px;
}

.bookmark a:hover {
    opacity: 1;
    color: #33b1ff;
    text-decoration: underline;
}

.bookmark button {
    background: linear-gradient(90deg, #4776e6, #8e54e9);
    color: white;
    text-shadow: 1px 1px 5px #121212;
    padding: 5px;
    width: 90%;
    margin: 3px;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
}

.bookmark button:hover {
    background: linear-gradient(90deg, #8e54e9, #4776e6);
}
#bookmarksContainer {
    text-align: center;
    width: 90%;
    border-radius: 10px;
    margin: auto;
    margin-top: 15px;
    padding: 25px;
}

#bookmarksContainer p {
    text-transform: uppercase;
    font-size: 16px;
    margin-bottom: 10px;
    opacity: 0.5;
}
#addBookmarkForm {
    background-color: #121212;
    text-align: center;
    width: 30%;
    margin: auto;
    margin-top: 40px;
    padding: 30px 20px;
    border-radius: 10px;
}

#bookmarkUrl, #bookmarkTitle {
    background-color: white;
    color: #121212;
    border: none;
    width: 90%;
    margin-bottom: 10px;
    padding: 8px;
    font-size: 16px;
}

#bookmarkUrl:focus, #bookmarkTitle:focus {
    outline: none;
}

#addBookmarkForm button {
    background-color: #f600ff;
    color: white;
    border: none;
    width: 90%;
    padding: 8px;
    font-size: 16px;
}

#addBookmarkForm button:hover {
    cursor: pointer;
    background-color: #f833ff;
}

@media (max-width: 768px) {
    #addBookmarkForm {
        width: 80%;
    }
}
#editBookmarkContainer {
    background-color: #121212;
    text-align: center;
    width: 40%;
    margin: auto;
    margin-top: 40px;
    padding: 50px 30px;
    border-radius: 10px;
}

#editBookmarkContainer h1 {
    margin-bottom: 25px;
}

#editBookmarkContainer input {
    background-color: white;
    color: #121212;
    border: none;
    font-size: 16px;
    width: 90%;
    padding: 10px;
    margin-bottom: 10px;
}

#editBookmarkContainer input:focus {
    outline: none;
}

#editBookmarkContainer button {
    border: none;
    width: 90%;
    padding: 10px;
    margin-bottom: 10px;
}

#saveEditBtn {
    background-color: hsl(203, 100%, 50%);
    color: white; 
}

#saveEditBtn:hover {
    cursor: pointer;
    background-color: hsl(203, 100%, 60%);
}

#deleteBtn {
    background-color: hsl(346, 100%, 50%);
    color: white;
}

#deleteBtn:hover {
    cursor: pointer;
    background-color: hsl(346, 100%, 40%);
}

@media (max-width: 768px) {
    #editBookmarkContainer {
        width: 80%;
    }
}
#myAccountContainer {
    text-align: center;
    background-color: #121212;
    border-radius: 10px;
    width: 90%;
    margin: auto;
    margin-top: 40px;
    padding: 30px 0px;
}

#profilePicture {
    margin: auto;
    padding-top: 22px;
    font-size: 42px;
    background-color: #bb86fc;
    border: 5px solid white;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

#myAccountContainer h1 {
    margin-top: 10px;
}

.line {
    width: 90%;
    margin: 25px auto;
    opacity: 0.1;
}

#myAccountContainer h2 {
    margin-bottom: 15px;
    text-transform: uppercase;
}

#myAccountContainer input {
    background-color: transparent;
    color: white;
    border: 2px solid #03dac6;
    margin: 10px;
    padding: 10px;
    width: 25%;
    font-size: 16px;
}

#myAccountContainer input:focus {
    outline: none;
}

#myAccountContainer button {
    padding: 10px;
    border: none;
    width: 15%;
    font-size: 16px;
    cursor: pointer;
}

.changeBtn {
    background-color: transparent;
    color: white;
    outline: 2px solid #03dac6;
    transition: 0.3s;
}

.changeBtn:hover {
    background-color: #03dac6;
    color: #121212;
}

#deleteAccountBtn {
    background-color: transparent;
    color: white;
    outline: 2px solid #ff003c;
    margin-top: 10px;
    transition: 0.3s;
}

#deleteAccountBtn:hover {
    background-color: #ff003c;
}

@media (max-width: 768px) {
    #myAccountContainer {
        width: 80%;
    }

    #myAccountContainer h2 {
        margin-bottom: 25px;
    }

    #myAccountContainer input {
        display: block;
        text-align: center;
        width: 80%;
        margin: auto;
        margin-top: 10px;
    }

    #myAccountContainer button {
        display: block;
        width: 80%;
        margin: auto;
        margin-top: 10px;
    }
}
